import { NavParams } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, ViewController } from 'ionic-angular';
import { CountryProvider } from '../../../providers/providers';

@IonicPage()
@Component({
  selector: 'page-select-country',
  templateUrl: 'select-country.html'
})
export class SelectCountryPage {
  _countryService : any;
  _currentCountries :any;
  _countriesHot:any;
  isNeedAll: boolean = false;
  constructor(public navCtrl: NavController,
     public viewCtrl: ViewController,
      countryService: CountryProvider,
    public navParams: NavParams) {

    this.isNeedAll = this.navParams.data.isNeedAll ;
    this._countryService = countryService;
    this._currentCountries = this._countryService.all();
    this._countriesHot = this._countryService.getHots();
  }

  searchCountry(ev){
    let val = ev.target.value;
    if (!val || !val.trim()) {
      this._currentCountries = this._countryService.all();
      this._countriesHot = this._countryService.getHots();
      return;
    }
    this._countriesHot = null;
    this._currentCountries = this._countryService.query(val);
  }

  selectCountry(item){
    this._countryService.setCurrentCountry(item);
    this.viewCtrl.dismiss(item);
  }

}



